import React, { useState } from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { AdapterLuxon } from '@mui/x-date-pickers/AdapterLuxon';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import AppUtils from './utils';
import RunCalendar from './RunCalendar';

export const AppContext = React.createContext(null);

function App() {
  const [distance, setDistance] = useState('21km');
  const [raceday, setRaceday] = useState(AppUtils.getDefaultRaceDay());
  const [events, setEvents] = useState(AppUtils.buildRunPlanData(raceday, distance));

  const handleOnChangeDistance = (event) => {
    let newDistance = event.target.value;
    setDistance(newDistance);
    setEvents(AppUtils.buildRunPlanData(raceday, newDistance));
  }

  const handleOnChangeRaceday = (newRaceday) => {
    setRaceday(newRaceday);
    setEvents(AppUtils.buildRunPlanData(newRaceday, distance));
  };

  return (
    <div className="App">
      <AppContext.Provider value={{ events: events }}>
        <div className='pick-a-day'>
          <FormControl sx={{ mr: 1 }}>
            <InputLabel id="select-distance-label">Chn c ly</InputLabel>
            <Select
              labelId="select-distance-label"
              id="select-distance"
              value={distance}
              onChange={handleOnChangeDistance}
              autoWidth
              label="Chn c ly"
            >
              <MenuItem value="21km">21 km</MenuItem>
              <MenuItem value="42km">42 km</MenuItem>
            </Select>
          </FormControl>
          <LocalizationProvider dateAdapter={AdapterLuxon} adapterLocale='vi'>
            <DatePicker
              label='Chn ngy tham d cuc ua'
              value={raceday}
              onChange={(newRaceday) => handleOnChangeRaceday(newRaceday)}
            />
          </LocalizationProvider>
        </div>
        <RunCalendar />
      </AppContext.Provider>
    </div>
  );
}

export default App;
